import "core-js/features/map";
import "core-js/features/set";
import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import App from "./App";

// Init VK  Mini App
bridge.subscribe(({ detail: { type, data }}) => {
	if (type === 'VKWebAppUpdateConfig') {
		const schemeAttribute = document.createAttribute('scheme');
		schemeAttribute.value = data.scheme ? data.scheme : 'bright_light';
		document.body.attributes.setNamedItem(schemeAttribute);
        var status_bar_color = 'dark';
        if (schemeAttribute.value == 'space_gray') status_bar_color = 'light';
        bridge.send("VKWebAppSetViewSettings", {"status_bar_style": status_bar_color});
	}
});

bridge.send("VKWebAppInit");

ReactDOM.render(<App />, document.getElementById("root"));
if (process.env.NODE_ENV === "development") {
  import("./eruda").then(({ default: eruda }) => {}); //runtime download
}
