import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
//import View from '@vkontakte/vkui/dist/components/View/View';
import { View, Panel, PanelHeader, Header, Group, Cell, Root, Input, Div, Button, Alert, Text, Headline } from '@vkontakte/vkui';

import Icon28MessageOutline from '@vkontakte/icons/dist/28/message_outline';
import '@vkontakte/vkui/dist/vkui.css';

class App extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            activeView: 'view1',
            popout: null,
            input1: '',
            input2: '',
            input3: '',
            input4: '',
            input5: '',
        };
        this.openPopout = this.openPopout.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.closePopout = this.closePopout.bind(this);
    }
    
    handleChange(event) {
        const target = event.target;
        this.setState({ [target.name]: target.value });
    }
    
    openPopout() {
        var counter = 0;
        
        if (this.state.input1.toLowerCase() === 'байкал') counter++;
        if (this.state.input2.toLowerCase() === 'панда') counter++;
        if (this.state.input3.toLowerCase() === 'китайский') counter++;
        if (this.state.input4.toLowerCase() === 'амазонка') counter++;
        if (this.state.input5.toLowerCase() === 'голубой') counter++;
        this.setState({
            popout: <Alert 
                actions={[{title: 'Отлично', autoclose: true, mode: 'cancel'}]}
                onClose={this.closePopout}>
                
                <h2>Угадайка!</h2>
                <p>Количество вопросов, на которые вы ответили правильно: {counter} из 5</p>
            </Alert>
            })
    }
    
    closePopout() {
        this.setState({popout: null})
    }
    
    
    render() {
        return (
          <Root activeView={this.state.activeView}>
            <View popout={this.state.popout} activePanel="panel-1-1" id="view1">
              <Panel id="panel-1-1">
                <PanelHeader>Игра "Угадайка!"</PanelHeader>
                <Group>
                <p style={{ padding: 16}}>Какое самое глубокое озеро?</p>
              <Input name='input1' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <p style={{ padding: 16}}>Какое нелепое животное имеет черно-белый окрас?</p>
              <Input name='input2' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <p style={{ padding: 16}}>Какой язык самый распространенный в мире?</p>
              <Input name='input3' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <p style={{ padding: 16}}>Какая река самая длинная в мире?</p>
              <Input name='input4' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                <Group>
                <p style={{ padding: 16}}>Какой цвет получится, если смешать синий и зеленый?</p>
              <Input name='input5' placeholder="Введите ответ.." onChange={this.handleChange}/>
                </Group>
                 <Div style={{ padding: 46}}>
                   <Button size="xl" mode="primary" onClick={this.openPopout}>Отправить ответы</Button>
                 </Div>
              </Panel>
            </View>
          </Root> 
        )
    }
}



export default App;

